.App {
  text-align: center;
}

.container {
  background-color: #d3d3d3;
  height: 100vh;
  position: relative;
}

.navbar {
  background-color: black;
  color: #fff;
  width: 100%;
  height: 50px;
}

.nav-header {
  padding: 10px;
  float: left;
  margin: 0;
}

.btn-wrapper {
  margin-left: 150px;
}

.chat-container {
  width: 100%;
  min-width: 320px;
  position: relative;
  max-height: 70vh;
  overflow-y: auto;
  bottom: 0;
}

.scroll {
  width: 100%;
  height: 10px;
}

#message-common {
  width: 200px;
  margin-top: 5px;
  margin-bottom: 5px;
  border-radius: 20px;
}

.sent {
  background-color: #7fff00;
  margin-left: auto;
  margin-right: 5px;
}

.received {
  background-color: #f5f5dc;
  margin-left: 5px;
  margin-right: auto;
}

.form-style {
  width: 100%;
  height: 18px;
  display: flex;
  position: absolute;
  bottom: 10px;
}

.send-input {
  width: 1000px;
  height: 20px;
  background: #fff;
}

.send-btn {
  width: 100px;
  height: 26px;
  background-color: #7fff00;
}

.name {
  color: #fff;
  font-weight: 700;
  margin-bottom: 2px;
}

.message-text {
  margin-top: 1px;
}

.btn-out {
 float: right;
 margin-right: 5px;
 margin-top: 13px;
}